<script>
import {createEventDispatcher, onMount} from "svelte";
import handle from "./index.js";

const dispatch = createEventDispatcher();
let active;
export let pos;
export let value;
export let tip;

function dragstart() {
    active = true;
    dispatch('active', true)
}

function dragend() {
    active = false;
    dispatch('active', false);
}

</script>

<style>
.sli-tip {
    position: absolute;
    bottom: 0px;
    background: #000;
    padding: 5px;
    border-radius: 5px;
    color: #fff;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.sli-tip::after {
    content: "";
    width: 0;
    bottom: -10px;
    left: calc(50% - 5px);
    position: absolute;
    height: 0;
    border: 5px solid transparent;
    border-top-color: #000;
}

.thumb {
    position: absolute;
    top: 50%;
    width: 0;
    height: 0;
}

.thumb-content {
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.thumb-content::before {
    content: "";
    position: absolute;
    width: 200%;
    height: 200%;
    -webkit-transform: translate(-25%, -25%) scale(0);
            transform: translate(-25%, -25%) scale(0);
    border-radius: 100vh;
    background: var(--thumb-background, #5784fd);
    opacity: 30%;
    -webkit-transition: -webkit-transform 100ms ease-in-out;
    transition: -webkit-transform 100ms ease-in-out;
    transition: transform 100ms ease-in-out;
    transition: transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out;
}

.thumb-content.active::before {
    -webkit-transform: translate(-25%, -25%) scale(1);
            transform: translate(-25%, -25%) scale(1);
}</style>

<div class="thumb"
     style={`left: ${pos * 100}%;`}
     use:handle
     on:dragstart={dragstart}
     on:dragend={dragend}
     on:drag={({ detail: v }) => (pos = v)}>
    {#if tip}
        <div class="sli-tip">{value}</div>
    {/if}
    <div class="thumb-content" class:active>
        <slot/>
    </div>
</div>
